import React, { useState, useRef, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { PDFDocument } from 'pdf-lib';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/esm/Page/TextLayer.css';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

function App() {
  const [file, setFile] = useState(null);
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [loading, setLoading] = useState(false);
  const [isDragOver, setIsDragOver] = useState(false);
  const [loadedPages, setLoadedPages] = useState(new Set());
  const [displayedPages, setDisplayedPages] = useState(3); // lazy   
  const [startPage, setStartPage] = useState(1);
  const [endPage, setEndPage] = useState(1);
  const [extracting, setExtracting] = useState(false);
  const fileInputRef = useRef(null);
  const loaderRef = useRef(null); //  lazy 

  // Intersection Observer lazy loading 
  useEffect(() => {
    if (!loaderRef.current) return;
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && numPages && displayedPages < numPages) {
          setDisplayedPages(prev => Math.min(prev + 3, numPages));
        }
      },
      { threshold: 1 }
    );
    observer.observe(loaderRef.current);
    return () => observer.disconnect();
  }, [displayedPages, numPages]);

  //     endPage 
  useEffect(() => {
    if (numPages) {
      setEndPage(numPages);
    }
  }, [numPages]);

  const onFileChange = (event) => {
    const { files } = event.target;
    if (files && files[0]) {
      setFile(files[0]);
      setPageNumber(1);
      setLoadedPages(new Set());
      setDisplayedPages(3);
      setStartPage(1);
    }
    event.target.value = '';
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragOver(false);
    
    const droppedFiles = e.dataTransfer.files;
    if (droppedFiles && droppedFiles[0]) {
      const droppedFile = droppedFiles[0];
      if (droppedFile.type === 'application/pdf') {
        setFile(droppedFile);
        setPageNumber(1);
        setLoadedPages(new Set());
        setDisplayedPages(3);
        setStartPage(1);
      } else {
        alert('PDF   .');
      }
    }
  };

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
    setLoading(false);
  };

  const onDocumentLoadError = (error) => {
    console.error('PDF  :', error);
    setLoading(false);
  };

  const onPageLoadSuccess = (pageNumber) => {
    setLoadedPages(prev => {
      const newSet = new Set([...prev, pageNumber]);
      return newSet;
    });
  };

  const extractPages = async () => {
    if (!file || !numPages) {
      alert('PDF   .');
      return;
    }

    if (startPage < 1 || endPage > numPages || startPage > endPage) {
      alert('   .');
      return;
    }

    setExtracting(true);

    try {
      // PDF  ArrayBuffer 
      const arrayBuffer = await file.arrayBuffer();
      
      // PDF  
      const pdfDoc = await PDFDocument.load(arrayBuffer);
      
      //  PDF  
      const newPdfDoc = await PDFDocument.create();
      
      //     
      for (let i = startPage - 1; i < endPage; i++) {
        const [copiedPage] = await newPdfDoc.copyPages(pdfDoc, [i]);
        newPdfDoc.addPage(copiedPage);
      }
      
      //  PDF   
      const pdfBytes = await newPdfDoc.save();
      
      //  
      const blob = new Blob([pdfBytes], { type: 'application/pdf' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `${file.name.replace('.pdf', '')}_pages_${startPage}-${endPage}.pdf`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
      
      alert(`${startPage} ${endPage}  .`);
    } catch (error) {
      console.error('PDF  :', error);
      alert('PDF    .');
    } finally {
      setExtracting(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 py-4">
      <div className="max-w-7xl mx-auto px-4 space-y-6">
        <div className="flex flex-col lg:flex-row gap-3">
          {/*     */}
          <div className="lg:w-5/6">
            <div className="bg-white rounded-lg shadow-lg px-6 py-3">
              {file ? (
                <div className="space-y-6">
                  <div className="flex items-center justify-between">
                    <h2 className="text-xl font-semibold text-gray-800">
                      {file.name}
                    </h2>
                    <div className="text-gray-600">
                       {numPages || '...'} 
                    </div>
                  </div>

                  <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    {/*    */}
                    <div className="lg:col-span-1">
                      <h3 className="text-lg font-semibold text-gray-800 mb-4"> </h3>
                      <div className="max-h-[50rem] overflow-y-auto space-y-4">
                        {loading && (
                          <div className="flex items-center justify-center p-8">
                            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                            <span className="ml-2 text-gray-600">PDF  ...</span>
                          </div>
                        )}
                        <Document
                        file={file}
                        onLoadSuccess={onDocumentLoadSuccess}
                        onLoadError={onDocumentLoadError}
                        onLoadStart={() => setLoading(true)}
                        loading={
                          <div className="flex items-center justify-center p-8">
                            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                            <span className="ml-2 text-gray-600">PDF  ...</span>
                          </div>
                        }
                        >
                        {numPages &&
                          Array.from(
                            new Array(Math.min(displayedPages, numPages)),
                            (el, index) => (
                              <div
                                key={`page_${index + 1}`}
                                className={`border-2 rounded-lg p-4 bg-white hover:shadow-md transition-shadow cursor-pointer ${
                                  pageNumber === index + 1
                                    ? 'border-blue-500 bg-blue-50'
                                    : 'border-gray-200'
                                }`}
                                onClick={() => setPageNumber(index + 1)}
                              >
                                <div className="flex items-center space-x-4">
                                  <div className="flex-shrink-0">
                                    <Page
                                      pageNumber={index + 1}
                                      width={120}
                                      renderTextLayer={false}
                                      renderAnnotationLayer={false}
                                      onLoadSuccess={() => onPageLoadSuccess(index + 1)}
                                      loading={
                                        <div className="w-30 h-40 bg-gray-200 rounded flex items-center justify-center">
                                          <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-500"></div>
                                        </div>
                                      }
                                    />
                                  </div>
                                  <div className="flex-1">
                                    <h3 className="text-lg font-semibold text-gray-800">
                                      {index + 1}  
                                    </h3>
                                  </div>
                                </div>
                              </div>
                            )
                          )}
                        </Document>
                      </div>
                      {/* lazy-load  */}
                      <div ref={loaderRef} className="h-12" />
                    </div>

                    {/*     */}
                    <div className="lg:col-span-2">
                      <h3 className="text-lg font-semibold text-gray-800"> </h3>
                      {pageNumber ? (
                        <div className="flex justify-center items-center mt-2">
                          <div className="flex border border-gray-300 rounded-lg shadow-lg bg-white w-full p-4 justify-center">
                              <Document
                                file={file}
                                onLoadSuccess={onDocumentLoadSuccess}
                                onLoadError={onDocumentLoadError}
                                onLoadStart={() => setLoading(true)}
                                loading={
                                  <div className="flex items-center justify-center p-8">
                                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                                    <span className="ml-2 text-gray-600">PDF  ...</span>
                                  </div>
                                }
                              >
                                <Page
                                  pageNumber={pageNumber}
                                  width={Math.min(window.innerWidth * 0.35 - 100, 540)}
                                  renderTextLayer={false}
                                  renderAnnotationLayer={false}
                                  className="w-full h-auto max-w-full"
                                  style={{ maxWidth: '100%', height: 'auto' }}
                                />
                              </Document>
                          </div>
                        </div>
                      ) : (
                        <div className="text-center text-gray-500 py-8">
                          <p>      .</p>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ) : (
                <div
                  className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors h-full flex items-center justify-center ${
                    isDragOver
                      ? 'border-blue-500 bg-blue-50'
                      : 'border-gray-300 hover:border-blue-400'
                  }`}
                  onDragOver={handleDragOver}
                  onDragLeave={handleDragLeave}
                  onDrop={handleDrop}
                >
                  <label
                    htmlFor="file-upload"
                    className="cursor-pointer flex flex-col items-center"
                  >
                    <svg
                      className="w-12 h-12 text-gray-400 mb-4"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12"
                      />
                    </svg>
                    <span className="text-lg font-medium text-gray-600">
                      PDF    
                    </span>
                    <span className="text-sm text-gray-500 mt-2">
                        : 10MB
                    </span>
                  </label>
                </div>
              )}
            </div>
          </div>

          {/*    */}
          <div className="lg:w-1/6">
            <div className="bg-white rounded-lg shadow-lg p-6 space-y-6">
              <div className="flex justify-center">
                <button
                  onClick={() => {
                    fileInputRef.current?.click();
                  }}
                  className="px-6 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors font-medium shadow-md"
                >
                  {file ? 'PDF  ' : 'PDF  '}
                </button>
              </div>
              <input
                ref={fileInputRef}
                type="file"
                accept=".pdf"
                onChange={onFileChange}
                className="hidden"
                id="file-upload"
              />

              {/*    */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-800 text-center">
                   
                </h3>
                
                <div className="space-y-3">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                       
                    </label>
                    <input
                      type="number"
                      min="1"
                      max={numPages || 1}
                      value={startPage}
                      onChange={(e) => setStartPage(parseInt(e.target.value) || 1)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                       
                    </label>
                    <input
                      type="number"
                      min="1"
                      max={numPages || 1}
                      value={endPage}
                      onChange={(e) => setEndPage(parseInt(e.target.value) || 1)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    />
                  </div>
                  
                  <div className="text-xs text-gray-500 text-center">
                    {numPages ? ` ${numPages}  ` : 'PDF  '}
                  </div>
                </div>
                
                <button
                  onClick={extractPages}
                  disabled={extracting || !file || !numPages || startPage > endPage || startPage < 1 || endPage > (numPages || 1)}
                  className={`w-full px-4 py-3 rounded-lg font-medium transition-colors ${
                    extracting || !file || !numPages || startPage > endPage || startPage < 1 || endPage > (numPages || 1)
                      ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                      : 'bg-green-500 text-white hover:bg-green-600 shadow-md'
                  }`}
                >
                  {extracting ? (
                    <div className="flex items-center justify-center">
                      <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                       ...
                    </div>
                  ) : (
                    ' PDF '
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
